@font-face {
    font-family: 'Caveat';
    src: url('font/Caveat-VariableFont_wght.ttf');
}

:root {
    transition: font-size 0.5s ease;
}

:root.light {
    --bg-base: #ECE3D4;
    --bg-footer: #E3D6C3;
    --border: #C7A26B;
    --title-font: #372A15;
    --title-sub-font: #5C4523;
    --main-text-title-font: #111827;
    --main-text-font: #374151
}

:root.dark {
    --bg-base: #333333;
    --bg-footer: #E3D6C3;
    --border: #C7A26B;
    --title-font: #372A15;
    --title-sub-font: #5C4523;
    --main-text-title-font: #111827;
    --main-text-font: #374151
}

body {
    background-color: var(--bg-base);
    color: var(--bg-color5);
}

.bg-transition {
    transition: background-color 0.3s ease, color 0.3s ease;
}

/*데스크탑*/
@media all and (min-width:1279px) {
    html {
        font-size: 18px;
        /* 기본 폰트 사이즈를 18px로 설정 */
    }
}

/*노트북 & 태블릿 가로 : 1024px-1279px*/
@media all and (min-width:1024px) and (max-width:1279px) {
    html {
        font-size: 16px;
        /* 기본 폰트 사이즈를 16px로 설정 */
    }
}

/*태블릿 가로 : 769px-1023px*/
@media all and (min-width:768px) and (max-width:1023px) {
    html {
        font-size: 15px;
        /* 기본 폰트 사이즈를 15px로 설정 */
    }
}

/*모바일 가로 & 태블릿 세로 : 480px-767px*/
@media all and (min-width:480px) and (max-width:767px) {
    html {
        font-size: 14px;
        /* 기본 폰트 사이즈를 14px로 설정 */
    }
}

/*모바일 : -479px*/
@media all and (max-width:479px) {
    html {
        font-size: 13px;
        /* 기본 폰트 사이즈를 13px로 설정 */
    }
}

/* 가로 */
@media all and (orientation: landscape) {
    header {
        border-bottom: 1px solid var(--border);
        padding: 0.5rem 1rem;
        font-size: 2rem;
        font-family: 'Caveat';
        font-weight: 900;
    }
}

/* 세로 */
@media all and (orientation: portrait) {
    header {
        border-bottom: 1px solid var(--border);
        padding: 0.5rem 1rem;
        font-size: 2rem;
        font-family: 'Caveat';
        font-weight: 900;
    }
}